.btn {
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 16px;
  border-radius: 4px;
  font-family: "TT Intrphss Pr Mn Trl Vr";
  font-weight: 400;
  font-size: 13px;
  line-height: 150%;
  letter-spacing: 0%;
  text-transform: uppercase;
  background-color: #182126;
  color: #fff;
  width: fit-content;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: 0.4s;
}
.btn::before {
  content: "";
  display: block;
  width: 100%;
  height: 10px;
  background-color: #182126;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.btn p,
.btn span {
  transition: all 0.45s cubic-bezier(0.23, 1, 0.32, 1), width 0.28s cubic-bezier(0.23, 1, 0.32, 1), left 0.28s cubic-bezier(0.23, 1, 0.32, 1), right 0.28s cubic-bezier(0.23, 1, 0.32, 1);
}
.btn p:nth-child(2) {
  border-radius: 4px;
  position: absolute;
  height: 0;
  width: 80%;
  left: 10%;
  right: 10%;
  background-color: #4d4d4d;
  color: #fff;
  bottom: 10%;
  z-index: 1;
  transform-origin: center;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.btn p:nth-child(2) span {
  transform: translate3d(0, 100%, 0) rotateX(45deg);
  transform-origin: 50% 100%;
}
.btn p:nth-child(2)::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  background-color: #4d4d4d;
  height: 13px;
  bottom: 0;
  left: 0;
  z-index: 1;
}
@media (hover: hover) {
  .btn:not(.disabled):hover p:nth-child(1) {
    translate: 0 -100%;
  }
  .btn:not(.disabled):hover p:nth-child(2) {
    height: 100%;
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
    translate: 0 0;
  }
  .btn:not(.disabled):hover p:nth-child(2) span {
    transform: translate3d(0, 0, 0) rotateX(0);
  }
}
.btn:not(.disabled):active p:nth-child(1) {
  translate: 0 -100%;
}
.btn:not(.disabled):active p:nth-child(2) {
  height: 100%;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  translate: 0 0;
}
.btn:not(.disabled):active p:nth-child(2) span {
  transform: translate3d(0, 0, 0) rotateX(0);
}
.btn.disabled {
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0.0509803922);
  color: rgba(0, 0, 0, 0.1490196078);
}
.btn.disabled::before {
  display: none;
}
.btn.white-on-dark {
  background-color: #fff;
  color: #182126;
}
.btn.white-on-dark::before {
  background-color: #fff;
}
.btn.white-on-dark p:nth-child(2) {
  background-color: #ff8522;
  color: #182126;
}
.btn.white-on-dark p:nth-child(2)::after {
  background-color: #ff8522;
}
.btn.white-on-orange {
  background-color: #fff;
  color: #182126;
}
.btn.white-on-orange::before {
  background-color: #fff;
}
.btn.white-on-orange p:nth-child(2) {
  background-color: #182126;
  color: #fff;
}
.btn.white-on-orange p:nth-child(2)::after {
  background-color: #182126;
}
.btn.orange {
  background-color: #ff8522;
  color: #182126;
}
.btn.orange::before {
  background-color: #ff8522;
}
.btn.orange p:nth-child(2) {
  background-color: #182126;
  color: #fff;
}
.btn.orange p:nth-child(2)::after {
  background-color: #182126;
}
.btn.transparent {
  border: 1px solid rgba(0, 0, 0, 0.1490196078);
  background-color: #bebebe;
  color: #182126;
}
.btn.transparent::before {
  background-color: #bebebe;
}
.btn.transparent p:nth-child(2) {
  background-color: #182126;
  color: #fff;
}
.btn.transparent p:nth-child(2)::after {
  background-color: #182126;
}
@media (hover: hover) {
  .btn.transparent:hover {
    border-color: transparent;
  }
}
.btn.transparent:active {
  border-color: transparent;
}

.icon-btn {
  width: 32px;
  height: 32px;
  flex: 0 0 32px;
