  content: "";
  display: block;
  width: 100%;
  height: 0;
  transition: 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  background-color: #182126;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
