}
@media (hover: hover) {
  .icon-btn:hover::after {
    height: 100%;
  }
}

.app-select {
  max-width: 220px;
  margin: 0 auto;
  width: 100%;
  position: relative;
}
.app-select.open .select__title {
  background-color: #182126;
  color: #fff;
}
.app-select.open .select__title svg {
  rotate: 180deg;
}
.app-select.open .select__title svg path {
  fill: #fff;
}
.app-select.open .select__content {
  opacity: 1;
  pointer-events: all;
}
.app-select.open .select__back {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
.app-select .select__title {
  position: relative;
  z-index: 1;
  transition: 0.4s;
  cursor: pointer;
  border-radius: 4px;
  background-color: #fff;
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 12px;
  color: #000;
  font-family: "TT Intrphss Pr Mn Trl Vr";
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 19.5px */
  letter-spacing: -0.143px;
  text-transform: uppercase;
}
.app-select .select__title span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.app-select .select__title svg {
  width: 16px;
  height: 16px;
  flex: 0 0 16px;
  transition: 0.4s;
  transform-origin: center;
  translate: 0 -2px;
}
@media (hover: hover) {
  .app-select .select__title:hover {
    background-color: #182126;
    color: #fff;
  }
  .app-select .select__title:hover svg path {
    fill: #fff;
  }
}
.app-select .select__content {
  padding: 24px;
  transition: 0.4s;
  opacity: 0;
  pointer-events: none;
  translate: 0 12px;
  position: absolute;
  top: 100%;
  left: 0;
  border-radius: 8px;
  border: 1px solid var(--white-55, rgba(255, 255, 255, 0.55));
  background: var(--white-35, rgba(255, 255, 255, 0.35));
  /* blur 16 */
  backdrop-filter: blur(8px);
  width: 280px;
}
.app-select .select__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;
  row-gap: 8px;
}
.app-select .select__item {
  cursor: pointer;
  color: var(--black-100, #182126);
  font-family: "TT Firs Neue";
  font-size: 14px;
  font-style: normal;
  font-weight: 450;
  line-height: 150%; /* 21px */
  letter-spacing: -0.14px;
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
}
.app-select .select__item.selected::before {
  background-color: #182126;
}
.app-select .select__item::before {
  content: "";
  display: block;
  width: 14px;
  height: 14px;
  flex: 0 0 14px;
  background-color: #fff;
  border-radius: 2px;
  transition: 0.4s;
}
.app-select .select__item::after {
  content: "";
  transition: 0.4s;
  display: block;
  width: 14px;
  height: 14px;
  flex: 0 0 14px;
  position: absolute;
  top: 50%;
  left: 0;
  translate: 0 -50%;
  border-radius: 2px;
  background: url(../img/icons/check.svg) center/contain no-repeat;
}

.app-checkbox input {
