.link {
  white-space: nowrap;
  color: #182126;
  transition: 0.5s;
  position: relative;
}
.link:hover::after {
  transform-origin: left;
  transform: scaleX(1) rotate(0.001deg);
}
.link::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  background-color: #182126;
  transition: 0.5s transform, 0.5s color;
  transform: scaleX(0) rotate(0.001deg);
  transform-origin: right;
}

