.home-hero {
  position: relative;
  /* Прогресс-бар */
}
.home-hero__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  pointer-events: none;
  z-index: 12;
  opacity: 0;
}
.home-hero__slide {
  user-select: none;
  height: 90dvh;
  position: relative;
  transition-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
  overflow: hidden;
}
.home-hero__slide::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  pointer-events: none;
  z-index: 2;
  transition-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
  transition: 0.4s;
  opacity: 0;
}
.home-hero__slide.swiper-slide-prev::before {
  opacity: 1;
}
.home-hero__slide.active .home-hero__visual {
  scale: 1;
}
.home-hero__slide.active .home-hero__title p {
  translate: 0;
}
.home-hero__visual {
  width: 100%;
  height: 100%;
  scale: 1.1;
  transition-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
  transition-duration: 1s;
}
.home-hero__visual img,
.home-hero__visual video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.home-hero__text {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 32px;
  bottom: 0;
  left: var(--container);
  z-index: 1;
  bottom: 98px;
}
@media (max-width: 568px) {
  .home-hero__text {
    bottom: unset;
    top: 225px;
  }
}
.home-hero__title {
  color: #fff;
  overflow: hidden;
}
.home-hero__title p {
  transition: 1.5s;
  transition-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
  translate: 0 100%;
}
.home-hero__pagination {
  bottom: 20px;
  gap: 8px;
  display: flex;
  position: absolute;
  width: fit-content;
  margin: 0 auto;
  left: var(--container);
  z-index: 10;
}
.home-hero__pagination > div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 14px;
  height: 14px;
  position: relative;
  opacity: 0.5;
  transition: 0.4s;
}
.home-hero__pagination > div svg {
  transition: 0.4s;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.home-hero__pagination > div.active {
  opacity: 1;
}
.home-hero__pagination > div.active svg {
  opacity: 1;
}
.home-hero__pagination .circle {
  --size: 6px;
  background-color: white;
  transition: 0.3s;
  margin: 0;
}
.home-hero .progress-container {
  z-index: 10;
  position: absolute;
}
.home-hero .progress-container circle {
  translate: 0 100%;
}
.home-hero .progress-circle {
  transform: rotate(-90deg);
  transition: stroke-dashoffset 0.1s linear;
}
.home-hero__banner {
  position: absolute;
  bottom: 20px;
  right: var(--container);
  z-index: 2;
  border-radius: 8px;
  border: 1px solid var(--white-12, rgba(255, 255, 255, 0.12));
  background: var(--white-25, rgba(255, 255, 255, 0.25));
  /* blur */
  backdrop-filter: blur(3px);
  color: #fff;
}
.home-hero__banner.step-2 .home-hero__clicker {
  display: none;
}
.home-hero__banner.step-2 .home-hero__form {
  display: flex;
}
.home-hero__banner.step-3 .home-hero__clicker {
  display: none;
}
.home-hero__banner.step-3 .home-hero__form {
  display: none;
}
.home-hero__banner.step-3 .home-hero__thanks {
  display: flex;
}
.home-hero__banner.hidden {
  display: none;
}
.home-hero__clicker {
  display: flex;
  gap: 32px;
  cursor: pointer;
  padding: 12px;
}
.home-hero__form {
  display: none;
  flex-direction: column;
  padding: 12px;
}
.home-hero__input {
  margin-top: 12px;
  margin-bottom: 8px;
}
.home-hero__check {
  display: flex;
  gap: 6px;
  color: rgba(255, 255, 255, 0.6980392157);
}
.home-hero__check a {
  color: rgba(255, 255, 255, 0.6980392157);
  border-bottom: 1px solid rgba(255, 255, 255, 0.6980392157);
}
.home-hero__thanks {
  display: none;
  gap: 32px;
  padding: 12px;
  color: #fff;
}
.home-hero__thanks button {
  width: 16px;
  height: 16px;
  flex: 0 0 16px;
}

.home-main {
  position: relative;
  z-index: 9;
}
.home-main .home-hero {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.home-page {
  background-color: #fff;
  height: 1000px;
  position: relative;
  z-index: 99;
}

html.lenis,
html.lenis body {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto !important;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-smooth iframe {
  pointer-events: none;
}